<template>
  <div
    id="merchandise-item"
    class="self-merchandise-item border position-relative p-3"
  >
    <!--  -->
    <div class="self-image-container">
      <img
        class="image-object-fit-contain"
        :src="imageSrc"
        :alt="alt"
        draggable="false"
      />
    </div>

    <!-- . -->
    <div
      class="badge bg-danger fs-6 fw-bold text-white p-1 rounded-3 position-absolute top-0 end-0 m-1"
    >
      {{ categoryName }}
    </div>

    <!--  -->
    <p class="fs-5 fw-bold text-black m-0 py-2">{{ name }}</p>

    <!--  -->
    <p class="fs-6 w-100 text-secondary text-truncate m-0 border-bottom py-2">
      {{ text }}
    </p>

    <!-- . -->
    <div
      class="self-price letter-spacing-px py-2 fs-5 fw-bold d-flex justify-content-between align-items-center"
      :class="{ 'special-offer': specialPrice }"
    >
      <!-- . -->
      <!-- <p class="self-special screen-hidden m-0" v-if="specialPrice">
        NT${{ getCurrency(specialPrice) }}
      </p> -->
      <p class="self-special screen-hidden m-0" v-if="specialPrice">
        NT$ <span v-to-currency-directive="specialPrice"></span> 
      </p>

      <!-- . -->
      <!-- <p class="self-original screen-hidden m-0">
        NT${{ getCurrency(originalPrice) }}
      </p> -->
      <p class="self-original screen-hidden m-0">
        NT$ <span v-to-currency-directive="originalPrice"></span> 
      </p>
    </div>

    <!--  -->
    <div class="self-link-container row">
      <!--  link . -->
      <div class="col-6 pe-1">
        <router-link
          class="d-block border fs-6 fw-bold px-2 py-1 rounded-3 text-reset text-decoration-none text-center"
          :to="merchandiseLink"
          ></router-link
        >
      </div>

      <!-- ,  1 . -->
      <div class="col-6 ps-1">
        <button
          class="btn border fs-6 fw-bold w-100 px-2 py-1 rounded-3"
          @click="
            clickHandler({
              id,
              category,
              categoryName,
              name,
              price: getCartprice(originalPrice, specialPrice),
            })
          "
        >
          
        </button>
      </div>
    </div>

    <!--  -->
    <div
      class="self-sold-out w-100 h-100 position-absolute top-0 start-0 d-flex justify-content-center align-items-center"
      v-if="remaining < 1"
    >
      <p class="letter-spacing-rem m-0 fs-1 fw-bold text-white text-center">
        
      </p>
    </div>
  </div>
</template>

<script>
// .

// .
import toCurrency from "../modules/toCurrency";

export default {
  name: "Merchandise",

  props: {
    //  id.
    id: {
      type: String,
      required: true,
    },

    // .
    category: {
      type: String,
      required: true,
    },

    // .
    categoryName: {
      type: String,
      required: true,
    },

    // .
    name: {
      type: String,
      required: true,
    },

    // .
    text: {
      type: String,
      required: true,
    },

    // .
    imageSrc: {
      type: String,
      required: true,
    },

    // .
    alt: {
      type: String,
      required: true,
    },

    // .
    remaining: {
      type: Number,
      required: true,
    },

    // .
    originalPrice: {
      type: Number,
      required: true,
    },

    // .
    specialPrice: {
      type: [Boolean, Number],
      required: true,
    },

    // .
    merchandiseLink: { type: String, required: true },
  },

  methods: {
    // .
    getCartprice(original, special) {
      return special ? special : original;
    },

    getCurrency(num) {
      return toCurrency(num);
    },

    // .
    async clickHandler({ id, category, categoryName, name, price, count = 1 }) {
      // .
      if (this.remaining < 1) return false;

      const result = await this.$store.dispatch("ADD_SHOPPING_CART_ACTIONS", {
        id,
        category,
        categoryName,
        name,
        price,
        count,
      });

      // , .
      this.consoleLogMixin("", result);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
@import "../assets/style/mixin.scss";
@import "../assets/style/class.scss";

.self-merchandise-item {
  // .
  .self-image-container {
    height: 10rem;
  }

  // .
  .self-price {
    // .
    .self-special {
      color: $green;
    }

    // , .
    &.special-offer .self-original {
      text-decoration-line: line-through;
    }
  }

  // 
  .self-link-container {
    a:active,
    button:active {
      color: $white !important;
      background-color: $green !important;
    }
  }

  // .
  .self-sold-out {
    background-color: $black-alpha;
  }
}

@media only screen and (min-width: $screen-width-xl) {
  .self-merchandise-item .self-price.special-offer .self-original {
    display: none;
  }
}
</style>
