<template>
  <table
    class="self-merchandise-table w-100 table table-hover text-center m-0 align-middle"
  >
    <thead>
      <tr class="fs-5">
        <th></th>
        <th></th>
        <th></th>
        <th v-if="isRemove"></th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="(item, i) of merchandises" :key="i">
        <td>{{ item.name }}</td>
        <td>{{ item.count }}</td>
        <td>NT $ {{ getCurrency(item.price) }} </td>
        <td v-if="isRemove">
          <button class="btn" @click="$emit('remove', item)">
            <i class="bi bi-archive"></i>
          </button>
        </td>
      </tr>
    </tbody>

    <tfoot>
      <tr>
        <!-- colspan . -->
        <td :colspan="isRemove ? 2 : 1"></td>
        <td colspan="2">NT$ {{ getCurrency(total) }} </td>
      </tr>
    </tfoot>
  </table>
</template>

<script>
// , .

// .
import toCurrency from "../modules/toCurrency";

export default {
  name: "MerchandiseTable",

  props: {
    // .
    merchandises: {
      type: Array,
      required: true,
    },

    // .
    total: {
      type: Number,
      required: true,
    },

    // .
    isRemove: {
      type: Boolean,
      required: true,
    },
  },

  methods: {
    getCurrency(num) {
      return toCurrency(num);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/style/variable.scss";
@import "../assets/style/mixin.scss";
@import "../assets/style/class.scss";

.self-merchandise-table {
  box-sizing: border-box;
}
</style>
