/**
 * , .
 * @param {num} num .
 */
export default function toCurrency(num) {
  let reverseStr = String(num)
    .split("")
    .reverse()
    .join("");
  let returnStr = reverseStr.replace(/\d{3}\B/g, (str) => {
    return str + ",";
  });

  return returnStr
    .split("")
    .reverse()
    .join("");
}
